import axios from 'axios'
import qs from 'qs'

export default function ajax (url, data={}, type='GET',json=false,headers={}) {
  return new Promise(function (resolve, reject) {
    // ajax
    let promise
    if (type === 'GET') {
      //url query
      if(data!=={}){
        let dataStr = '' //
        Object.keys(data).forEach(key => {
          dataStr += '/' + data[key]
        })
        if (dataStr !== '') {
          url = url + dataStr
        }
      }
      //IE
      const time = new Date().getTime()
      //url = url + '?' + 'time=' + time
      // get
      promise = axios.get(url,{
        headers
      })
    } else if(type === 'POST'){
      // post
      if(json)
        promise = axios.post(url, qs.stringify(data))
      else{
        promise = axios.post(url, data,{
          headers
        })
      }
    }else if(type === 'PUT'){
      // put
      if(json)
        promise = axios.put(url, qs.stringify(data))
      else
        promise = axios.put(url, data)
    }else if(type === 'DELETE'){
      // delete, java {data: param}
      //url  {params: param}urlhttp:www.XXX.com?a=..&b=..
      //promise = axios.delete(url, {data})
      promise = axios.delete(url, {params:data})
    }
    promise.then(function (response) {
      // resolve()
      resolve(response.data)
    }).catch(function (error) {
      //reject(error)
      resolve(error)
    })
  })
}



