import ajax from './ajax'

const Base = "http://172.16.13.73:8222"
export const LOGIN = (userName,password) => ajax(Base+"/login",{userName,password},'GET')               //

/**/
//
export const REQVIDEO = (data) => ajax(Base+"/video/videos",data,'GET')                                       //
export const REQONEVIDEO = (data) => ajax(Base+"/video/video",data,'GET')                                     //ID

//
export const REQFILES = (data) => ajax(Base+"/course/courses",data,'GET')                                      //
export const REQFILE = (data) => ajax(Base+"/course/course",data,'GET')                                       //


export const REQCOMMENT = (data) => ajax(Base+"/comment/comment",data,'GET')                                  //
export const UPLOADCOMMENT = (data) => ajax(Base+"/comment/comment",data,'POST')                              //


/**/
//
export const UPLOADVIDEO = (data) => ajax(Base+"/video/video",data,'POST')                                    //
export const UPDATEVIDEO = (data,videoId) => ajax(Base+"/video/video"+videoId,data,'PUT')                     //
//
export const UPLOADFILE = (data) => ajax(Base+"/course/course",data,'POST')                                   //



